@import "../../assets/scss/init.scss";
@import "../../assets/scss/fonts";

$speciication__wrap--max-width: 300px;
$speciication__src-internal--width: 300px;
$speciication__circle--size: $speciication__wrap--max-width*0.38;

.speciication__wrap--position {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;


  .speciication-selected-banner_wrap--decor {
    width: $speciication__wrap--max-width;
    margin: 1rem;
    padding: 1rem;
    border: 1px solid $color-secondary-2-2;
    border-radius: 1rem;

    .speciication-model__title-name {
      width: 100%;
      display: inline-block;
      text-align: center;
      margin: 0 auto;
      border-bottom: 1px solid $color-secondary-2-2;
      color: $color-secondary-2-0;
      /* background-color: rgb(231, 195, 195); */
    }

    .speciication-model__title-frequency {
      /* font-family: "Lobster-Regular"; */
      color: $color-secondary-2-0;
      text-align: center;
    }

    .speciication-model__info-wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .speciication-model__picture {
        margin: 1rem;
        width: $speciication__circle--size;
        height: $speciication__circle--size;
        /* border: 1px solid $color-secondary-2-3; */
        box-shadow: 0px 1px 10px $color-secondary-2-3;

        /*       background-image: url('../../assets/img/type2_200px.png');
      background-position: 8px 0;
      background-repeat: no-repeat;
      background-size: $speciication__wrap--max-width*.45 $speciication__wrap--max-width*.45; */

        border-radius: 50%;
        /*       padding: 3rem; */
        border: 1 solid red;

        &>img {
          padding: 1rem;
          width: $speciication__circle--size;
          height: $speciication__circle--size;
        }
      }
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .speciication-model__package {
        font-size: .9rem;
        color: $color-secondary-2-0;

        &>span {
          font-family: "Raleway-SemiBold";
          font-size: .75rem;
        }
      }

      .speciication-model__features {
        margin-top: 1rem;
        color:$color-secondary-1-4;
      }
    }

  }
}

.button-specification_wrap {
  display: flex;
  justify-content: space-between;
  /* margin: 1rem; */
  .button-specification_btn {
    margin: 1rem 0 1rem 0;
    height: 3rem;
  }

}

.specification-form__wrap {
  /*     display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; */
      .radiogroup--decor {
          /* background-color: #52520011; */
          width: 91%;
          margin: 1rem;
          border: 1px solid rgba(0, 0, 0, 0.336);
          border-radius: 6px;
      }

  }


.speciication-mail__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;


  .speciication-mail_title{
    /* background-color: $color-secondary-2-0; */
    color: $color-secondary-2-0;
    border-bottom: 1px solid $color-secondary-2-0;
    display: inline-block;
    width: $speciication__src-internal--width;
    text-align: center;
    font-family: Raleway-SemiBold;
  }
  .button-specification_wrap {
    margin: 2rem 0 2rem 0;
  }
}  
.speciication-selected__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  pad: 1rem;
  
  .speciication-selected--size {
    width: $speciication__src-internal--width;
  }

  .speciication__head-table {
    background-color: $color-secondary-2-0;
    color: white;
    width: $speciication__src-internal--width;
    text-align: center;
    display: inline-block;
    font-size: 1rem;
    padding: .5rem;
  }
}

.speciication-banner2__wrap--position {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .speciication-banner2-selected_wrap--decor {
    width: $speciication__src-internal--width;
    margin: 1rem;
    padding: 1rem;
    border: 1px solid $color-secondary-2-2;
    border-radius: 1rem;
    .speification-banner2__title {
      color: $color-secondary-2-0;
      text-align: center;
      border-bottom: 1px solid $color-secondary-2-0;
      margin-bottom: 1rem;
    }
    .speification-banner2__row {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      font-size: .9rem;
      color: $color-secondary-2-0;

      .speification-banner2__row--name {
        font-family: "Raleway-SemiBold";
        font-size: .75rem;
      }
    }
  }
}
.xy {
  width: 100px;
}
.specification-phaseNoise__row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: $speciication__src-internal--width;
  /* margin-left: 1rem; */

}

.specification-form__wrap--position{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
 }